@use 'var';
@use 'hero';
@use 'shows';
@use 'plans';
@use 'available-devices';
@use 'faq';
@use 'footer';


@font-face {
    font-family: Avenir;
    src: url(/build/fonts/Avenir-Regular.woff2);
    font-weight: 400;
}

@font-face {
    font-family: Avenir;
    src: url(/build/fonts/Avenir-Bold.woff2);
    font-weight: 700;
}



*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Avenir, sans-serif;
}


body {
    background-color: var.$corDeFundo;
    
}

@mixin text($fontSize: 16px) {
    font-size: $fontSize;
    line-height: $fontSize + 10px;
    color: var.$corTextoPrincipal;
}
@mixin title($fontSize: 28px) {
    @include text($fontSize);
    color: var.$corTextoSecundario;
}

.text--big {
    @include text(18px);

}
.text {
    @include text;
}

.text--small {
    @include text(12px);
}
.title--big {
    @include title(44px);
}

.title {
    @include title(40px);
}

.title--small {
    @include title(28px);
}

.link-text {
    @include text(18px);
    color: var.$corTextoSecundario;
    text-decoration: underline;
}
.button {
    display: block;
    width: 100%;
    height: 60px;
    font-size: 18px;
    color: var.$corTextoSecundario;
    background-color: #6421ff;
    text-transform: uppercase;
    border-radius: 4px;
    margin-top: 2vw;
    margin-bottom: 8px;
    border: none;
    cursor: pointer;
    ;
}

.image-text-section {
    padding: 5.6vw;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    
    img {
        max-width: 100%;
    }
    
    &__text_container {
        padding: 0 2vw;
   
        h2 {
            margin-bottom: 24px;
        }
   
    }

    &--image-full-width {
        display: block;
        position: relative;
        
        img {
            width: 100%;
                     
        }
        
        &__text_container {
            position:absolute;
            top: 0;
            left: 50%;
            height: 100%;
            width: calc(50% - 5.6vw );
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }

    
}