.plans {
    padding: 5.6vh;

    h2 {
        text-align: center;
    }
    
    &__list {
        display: flex;
        justify-content: space-around;
        margin-top: 50px ;
        
        &__item {
            
            text-align: center;
            padding: 60px 44px 44px;
            max-width: 380px;
            width: 100%;

            &:nth-child(2) {
                background-image: url(/build/images/fundo_combo_plus.png);
                background-repeat: no-repeat;
                background-size: contain;
            }

            img {
                height: 83px;
            }
            
            strong {
                display: block;
                margin-top: 24px;
            }
        }
    }
    p {
        text-align: center;
    }
}